.contact.section{
    padding-bottom: 6.25rem;
}
.contact_container{
    grid-template-columns: 4fr 8fr;
    column-gap: 1.875rem;
}
.contact_title{
    font-size: var(--h3-font-size);
    margin-bottom: 1rem;
    margin-top: 3rem;
}
.contact_details{
    padding-bottom: 2rem;
}
.contact_form-group{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.5rem;
}
.contact_form-div{
    position: relative;
    margin-bottom: 1.875rem;
    height: 3.75rem;
}
.contact_form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: var(--shadow);
    background-color: var(--container-color);
    color: var(--text-color);
    border: none;
    outline: none;
    border-radius: 1.875rem;
    padding: 0.625rem 1.875rem;
    z-index: 1;
    appearance: none;
    transition: all 0.3s ease-in-out;
}
.contact_form-area{
    height: 10.25rem;
}
.contact_form-area textarea{
    resize: none;
}
.fa-paper-plane{
    color: #787878;
}
.success-message,
.error-message {
  background-color: rgb(37, 141, 37); /* Green for success, adjust for errors */
  color: white;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
  position: fixed; /* Adjust positioning as needed */
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999; /* Ensure message appears above other content */
}

.error-message {
  background-color: #db220d; /* Red for error, adjust as needed */
}
@media screen and (max-width:1024px){
    .contact_container{
        grid-template-columns: 300px 360px;
        justify-content: center;
    }
    .contact_form-group{
        grid-template-columns: 1fr;
    }
 }
 @media screen and (max-width:768px){
    .contact_container{
        grid-template-columns: 310px;
        row-gap: 1.875rem;
    }
    .contact_info{
        text-align: center;
    }
    .contact_details{
        text-align: center;
    }
 }

 @media screen and (max-width:350px){
    .contact_container{
        grid-template-columns: 1fr;
    }
    
 }



  